<template>
  <div>
    <VideoPlayer />
    <!-- <video controls class="video" :src="require(`../assets/video/${id}.mp4`)"></video> -->
    {{ `../assets/video/${id}.mp4` }}
    <!-- <video controls class="video" src="@/assets/video/42342.mp4" :key="videoPath"></video>
    <video controls class="video" src="../assets/video/42342.mp4" :key="videoPath"></video> -->
    <!-- <video controls class="video" ref="video" :key="videoPath" v-html="fullUrl">
    </video> -->
    <button @click="changeVideo(videoPath)">CHANGE</button>
    <Loader />
  </div>
</template>

<script>
import VideoPlayer from '@/components/VideoPlayer.vue';
import Loader from '@/components/Loader.vue';

export default {
  name: 'VideoPage',
  components: {
    VideoPlayer,
    Loader
  },
  data() {
    return {
      src: '',
      isVideoExist: false,
    }
  },
  computed: {
    fullUrl() {
      return `<source :src="${this.videoPath}" type="video/mp4">`
    },
    videoPath() {
      return `../assets/video/${this.id}.mp4`;
    },
    id() {
      return this.$route.params.id
    }
  },
  methods: {
    checkVideoExistence() {
      const img = new Image();
      img.src = this.videoPath;
      img.onload = () => {
        // Video file exists, do something
        this.isVideoExist = true;
        this.$refs.videoPlayer.load(); // Reload the video player
      };
      img.onerror = () => {
        console.log('Video file does not exist');
      };
    },
    changeVideo(newSrc){
      this.$data.src = newSrc;
      //Force video load.
      debugger
      var vid = this.$refs.video;
      vid.load();
    }
  },
  created() {
    console.log(this.id);
    this.checkVideoExistence(this.id);
    // this.id = this.$route.params.id;
  }
}
</script>

