
<template>
  <div>
    <video ref="videoElement" class="video-js"></video>
  </div>
</template>

<script>
export default {
  mounted() {
    const player = this.$videojs(this.$refs.videoElement, {
      // Video.js options
    });
    player.src({ type: 'video/mp4', src: '@/assets/video/42342.mp4' });
  },
  beforeDestroy() {
    if (this.player) {
      this.player.dispose();
    }
  },
};
</script>

<style>
.video-js {
  width: 100%;
  height: 300px; /* Set the desired video player height */
}
</style>
